body{
    font-family: 'Poppins';
     background-color: #fff;
    ;
}

button{ 
    font-family: 'Poppins';
    width:8%;
    height:35px;
    border-radius:14px;
    border:none;
    cursor:pointer;
    color:black;
    background:white;
    font-family: poppins;
}
button:hover{
    background:#cd8764;
    color:black
}
.splashEnable{
    background-color:#a4508b;
    width:100%;
    height:100vh;
    position:fixed;
    z-index:1000;
    font-family: poppins;
}
.splashDisable{
    display:none;
}
select{
    border:none;
    width:200px;
    cursor:pointer;
    border-radius: 10px;
    height: 30px;
    font-family: poppins;
}
.menu{
  color: black;
  padding: 5px;box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 6px;
}
.mainDiv{
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: 90vh;
    align-items: baseline;
    margin:auto;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    font-family: poppins;
    flex-direction: column;
}
input{
    height:30px;    
    border: none;
    border-radius: 6px;
    outline:none;
    font-family: poppins;
}
.nav{
    width: 100%;
    height: 70px;
    background: #ffffff;
}

.menuDiv{
    background-color: #292728;
    animation: menuDeAnimation 1s;
    height: 0vh;
    position: absolute;
    width: 100%;
  }
  .menuDivActive{
    background-color: #292728;
    color: black;
    animation: menuAnimation 1s;
    height: 25vh;
    position: absolute;
    width: 100%;
  }
  
  @keyframes menuAnimation {
    from {
      height: 0vh;
    }
    to {
      height: 25vh;
    }
  }
  
  @keyframes menuDeAnimation {
    from {
      height: 25vh;
    }
    to {
      height: 0vh;
    }
  }
  h6{
      background: transparent;
      color: white;
      margin-top: 3%;
      font-size: 18px;
      cursor: pointer;
      position: absolute;
      font-weight: 400;
      margin-left: 5%;
  }
  h6:hover{
      color: #cd8764;
  }
  .cards{
      display: flex;
      flex-wrap: wrap;
    width: 90%;
    height: 93%;
    background: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 14px;
    margin: auto;
    align-items: space-between;
    justify-content: space-around;
  }
  .card{
      width: 30%;
      height: 44%;
      background: linear-gradient(315deg, #f8ceec 0%, #a88beb 74%);
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
      border-radius: 14px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 2%;
      cursor: pointer;
  }
  .card button{
    align-items: center;
  }
  @media screen and (max-width: 700px){
      .card{
          font-size: 10px;
      }
  }
  svg{
      cursor: pointer;
  }
  path{
      
    stroke: rgb(83, 82, 82);
    stroke-width: 2;
  }
  svg:hover{
      outline: 1px solid black;
  }
  path:hover{
      stroke: rgb(0, 0, 0);
     
  }
  .popup{
    display: block;
    width: 60vw;
    height: 50vh;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    position: absolute;
    top: 25vh;
    left: 20vw;
    color: black;
  }
  @media screen and (max-width:600px) {
    .popup{
      height: 100vh;
      width: 100vw;
      
    }
    svg{
      width: 10vw;
      height: 10vh;
    }
  }
  .popdown{
    display: none;
  }
  .popdiv{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    backdrop-filter: blur(10px);
  }
  svg{
    width:2vw
  }
  
.heart {
  display: block;
  position: relative;
  width: 100px;
  height: 90px;
  animation: heartbeat 1s infinite;
  margin:0 auto
}
.heart:before,
.heart:after {
  position: absolute;
  content: "";
  left: 50px;
  top: 0;
  width: 50px;
  height: 80px;
  background: rgb(255, 0, 0);
  -moz-border-radius: 50px 50px 0 0;
  border-radius: 50px 50px 0 0;
  -webkit-transform: rotate(-45deg);
     -moz-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
       -o-transform: rotate(-45deg);
          transform: rotate(-45deg);
  -webkit-transform-origin: 0 100%;
     -moz-transform-origin: 0 100%;
      -ms-transform-origin: 0 100%;
       -o-transform-origin: 0 100%;
          transform-origin: 0 100%;
}
.heart:after {
  left: 0;
  -webkit-transform: rotate(45deg);
     -moz-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
       -o-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transform-origin: 100% 100%;
     -moz-transform-origin: 100% 100%;
      -ms-transform-origin: 100% 100%;
       -o-transform-origin: 100% 100%;
          transform-origin :100% 100%;
}



@keyframes heartbeat
{
0%
{
  transform: scale( .75 );
}
20%
{
  transform: scale( 1 );
}
40%
{
  transform: scale( .75 );
}
60%
{
  transform: scale( 1 );
}
80%
{
  transform: scale( .75 );
}
100%
{
  transform: scale( .75 );
}
}
.heartdown{
  display: none;
}
.hear:before, 
.hear:after{
    content:"";
    position:absolute;
    left:50%;
    top:30%;
}
.hear{
  position:relative;
  left:65%;
  width:70px;
  height: 100px;
  border-left:4px solid red;
  border-top:4px solid red;
  transform:rotate(-45deg);
  border-top-left-radius:50%;
  border-top-right-radius:50%;
  cursor: pointer;
}

.hear:after{
  top:11px;
  left:11px;
  width:70px;
  height: 100px;
  border-right:4px solid black;
  border-top:4px solid black;
  transform:rotate(90deg);
  border-top-right-radius:50%;
  border-top-left-radius:50%;
  /*-webkit-animation: break 4s infinite;
  -webkit-animation-play-state: paused;*/
}

.hear:hover:after{
  /*-webkit-animation-play-state: running;*/
  animation: break 4s linear 1;
}

@keyframes break{
  0%{transform:rotate(90deg);}
  8%{
    transform-origin:50% 85%;
    transform:rotate(140deg);
  }
  20%{
    transform-origin:80% 90%;
    transform:rotate(180deg);
  }
  40%{
    transform-origin:55% 90%;
    transform:rotate(150deg);
  }
  65%{
    transform-origin:70% 90%;
    transform:rotate(170deg);
  }
  95%{
    transform-origin:60% 90%;
    transform:rotate(155deg);
  }
  100%{
    transform-origin:50% 50%;
    transform:rotate(90deg);
  }
}
.hear p{
  font-size: 24px;
  position: absolute;
  /* margin-right: 16vw; */
  left: 2vw;
}

